.presentation{display:grid;height:100%;grid-template-areas:"header" "main" "footer";grid-template-rows:auto 1fr auto}.presentation header{grid-area:header}.presentation footer{grid-area:footer}.presentation main{grid-area:main}

header{padding-left:1em;padding-right:1em;background-color:#03a9f4}

footer{display:grid;grid-template-columns:1fr auto;background-color:#03a9f4;align-content:center;flex-direction:row;padding-right:1em;padding-left:1em}

h3{font-weight:bold;font-size:2em}html,body,#root{height:100%;width:100%;margin:0;font-family:"Roboto", "Helvetica", "Arial", "sans-serif"}

